@import "../variables", "../mixins", "../fonts";

* {
    box-sizing: border-box;
}

.wp-die-message,
p:not(.interface-interface-skeleton__content p) {
    color: $blackcolor;
    font-size: $fontsize;
    line-height: $lineheight20;

    a {
        color: $primarycolor;
    }
}

a,
a:hover,
.field-move .menus-move,
.components-button.is-secondary:not(.block-editor-link-control__search-item-action),
.components-button.is-tertiary {
    text-decoration: none;
}

a,
.button,
input,
select,
textarea,
label,
.adminify-copy-btn,
.clear-button {
    box-shadow: none;
    min-height: inherit;

    &:hover,
    &:focus,
    &:visited,
    &:active {
        box-shadow: none !important;
        outline: 0 !important;
    }
}

:root {
    --wp-admin-border-width-focus: 0;
}

img:not(.plugin-icon) {
    // height: auto;
    max-width: 100%;
}

.field.is-grouped {
    display: flex;
}

.is-rounded {
    border-radius: 9999px;
}

.is-inline-block {
    display: inline-block;
}

.nav-tabs,
.is-flex {
    display: flex;
}

.is-inline-flex {
    display: inline-flex;
}

.is-boxed.is-centered {
    display: flex;
    align-items: center;
    justify-content: center;
}

.dropdown-menu {
