input,
textarea,
select,
.form-table .select2-container .select2-selection,
.wp-adminify-top-header--search--form input,
&.modal-open input[type="text"],
&.modal-open input[type="search"],
.wp-adminify-dashboard-widgets .chosen-choices,
.adminify-section[data-section-id] .adminify-data-wrapper .adminify-cloneable-content .adminify--result,
