.postbox-container #post_tag input[type="text"],
.media-modal .media-frame-content input[type="search"],
.media-frame-content .media-sidebar .compat-item input[type="text"],
.media-frame-content .settings input,
.media-frame-content .settings textarea,
.media-frame-content .setting input,
