            }
        }
    }

    .search-form .search-box {
        margin: 0;
    }

    .wp-list-table {
        .delete{
            a {
                background-color:$redlight;
                border-color: $redlight;
                color: $whitecolor;
            }
        }
    }

    .wp-list-table.widefat .inline-edit-save button.cancel,
    .comment-reply .reply-submit-buttons button.cancel,
    .widget-control-actions .button-link.widget-control-close,
    .wp-adminify-dialog-cancel.wp-adminify-dialog-confirm-cancel {
        background-color: $reddark;
        border-color: $reddark;
        border-radius: $radius6p;
        color: $whitecolor;
        @include font14_600;

        &:hover {
            background-color: $whitecolor;
            color: $reddark;
        }
    }

    .bulk-actions {
        label {
            background-color: transparent;
            border: 0;
            margin: 0;
            padding: 0;
        }

        input.menu-items-delete.disabled {
            display: block;
            padding: 0;
        }
    }

    .nav-tab-wrapper {
        .nav-tab {
            color: $titlecolor;
        }

        .nav-tab-active {
