    @include font14_400;
    line-height: $lineheight20;
    height: auto;
    padding: 8px 1rem;
}

.adminify-copy-btn {
    .icon {
        font-size: $fontsize + 2px;
        height: auto;
        width: auto;
        line-height: 1;
        margin: 0;

        &:before {
            line-height: $lineheight20 + $lineheight16 - 4px;
        }
    }
}

hr {
    border-top: 1px solid $whitegray;
    border-bottom: 0;
    display: inline-block;
    width: 100%;
}

.adminify-compability {
    border-radius: $radius6p - 3px;
    color: $whitecolor;
    float: left;
    font-size: 0;
    height: 20px;
    width: 20px;
    margin-right: 10px;
    position: relative;

    .dashicons {
        font-size: $fontsize + 4px;
        line-height: $lineheight20;
    }

    &.enable {
        .dashicons {
            font-size: $fontsize + 2px;

            &:before {
                content: "\f15e";
            }
        }
    }
}

.enable {
    background: $greencolor;
}

.disable {
    background: $reddark;
}

.dashicons:before {
    font-family: dashicons;
}

// Theme / Plugin Editor right sidebar

#templateside {
    li {
        background-color: $whitecolor;
        color: $black;
        font-size: $fontsize - 1px;
        line-height: $lineheight20;

        a {
            color: $primarycolor;

            .nonessential {
                color: $blackmuted;
                @include font12;
                line-height: $lineheight16;
            }
        }

        .notice {
            background-color: $graycolor;
            border-radius: 0;
        }
    }
}

// Switch
.admnify-switcher {
    display: inline-block;
    height: 20px;
    width: 36px;
    margin-top: -1px;
    position: relative;

    input {
        height: 0;
        width: 0;
        opacity: 0;

        &:checked+.slider:before {
            transform: translateX(16px);
        }
    }

    .slider {
        background-color: $whitegray;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        position: absolute;
        transition: 0.4s;

        &.round {
            border-radius: $radius6p + 10px;

            &:before {
                border-radius: 50%;
            }
        }

        &:before {
            content: "";
            background: $whitecolor;
            height: 18px;
            width: 18px;
            left: 1px;
            bottom: 1px;
            position: absolute;
            transition: 0.4s;
        }
    }
}

.fade {
    opacity: 0;
    transition: opacity 0.35s linear;
}

.fade.active {
    opacity: 1;
}

.collapse:not(.show) {
    display: none;
}

.collapsing {
    height: 0;
    overflow: hidden;
    transition: height 0.35s ease;
}

.fs-secure-notice {
