.wp-adminify--popup-area {
    background-color: rgba($color: #000000, $alpha: 0.75);
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    opacity: 0;
    position: fixed;
    z-index: -1;

    transition: opacity 0.3s ease-in-out;

    .wp-adminify--popup-container_inner {
        max-width: 600px;
        margin: 0 auto;
        padding: 1.875rem;
        position: relative;
    }

    .wp-adminify--popup-close {
        color: $reddark;
        top: .5rem;
        right: 1rem;
        position: absolute;

        .dashicons {
            font-size: 1.875rem;
        }
    }
}

&.wp-adminify--popup-show {
    .wp-adminify--popup-area {
        opacity: 1;
        z-index: 9999;
    }
}
